import React, { useEffect } from 'react'

const MobileAppScript = ({token}) => {
    function sendUserTokenToReactNative(userToken) {
        // Send the user token to React Native
        window.ReactNativeWebView.postMessage(JSON.stringify({ type: 'USER_TOKEN_CHANGED', token: userToken }))
      }
    useEffect(()=>{
        // Call this function when user logs in
        function notifyUserLoggedIn() {
            // Send an event to indicate user login
            sendUserTokenToReactNative(token)
        }
        if (token) notifyUserLoggedIn();
    }, [token])
}
export default MobileAppScript